
.App {
    text-align: center;
}

.App > div:nth-of-type(1) {
    text-align: center;
    background-color: #222;
    padding: 1rem;
    color: white;
}

.App > div:nth-of-type(1) a {
    text-decoration: none;
    color: white;
    margin-left: 1rem;
}

.App > div:nth-of-type(2) {
    padding: 2rem;
}



